				"html": "<p><strong><em>Concept</em></strong></p>\n<ul>\n<li>How can we provide the minimum necessary education to all people?</li>\n<li>What is the way of education to make use of each person in the changing world?</li>\n<li>What is learning that increases human possibilities?</li>\n</ul>\n<br>\n<p>* * * * * * * * * *</p>\n<br>\n<p>When examining the problem of education, first \"Educational Gap\" is mentioned.\nThis is that some people don't enough educated.\nIn general it is thought that there is a correlation with economic disparity as the main cause.</p>\n<p>I have doubts about this.\nCertainly, they are hard to get educated if they are in absolute poverty.\nHowever, correlation and causal relationship are different.\nIn the case of  simply disparity (gap) is an accurate causal relationship recognized?</p>\n<p>I think that it is actually a paradigm problem rather than a money problem.\nPeople are affected by the surrounding environment.\nIf people around him think so, he is also likely to think the same way.\nFor example, low-level education and low wages.\nPeople don't learn in a group of people who don't learn.\nPeople don't want to earn money in a group of people who don't' want to earn money.\n(Whether it is not always good to earn money is another)</p>\n<p>In fact, there seems to be data that the economic disparity does not shrink even if the education gap narrows.</p>\n<p>Also, more advanced education is not absolutely necessary to earn money.\nMoney is necessary to gain advanced knowledge and skills.\nYou can earn money with it, but there is a way to earn it without using it too.\nAnd, many things should be possible to learn without money (or for a small sum).</p>\n<p>So, the important thing is \"Paradigm\".  </p>\n<p>・・・Making your own future with your own hands.</p>",
